.category-breadcrumb {
    color: rgb(112, 112, 112);
    height: 40px;
    display: flex;
    align-items: center;
    height: 50px;
    letter-spacing: -0.5px;

    .category-link {
        color: rgb(16, 16, 16);
        font: 400 18px/30px Ubuntu !important;
        margin-right: 5px;
        text-decoration: none;
        letter-spacing: -1.2px;
        align-items: center;
        justify-content: center;
    }
}

.blog-content-screen-container-no-side-bar {
    background-color: white;
    width: fit-content;
    height: fit-content;
    min-height: 100vh;
    min-width: 100vw;

    .article-embed-youtube-video {
        width: calc(100%);
        height: 550px !important;
        margin-top: 15px;

        @media screen and (min-width: 1400px) {
            height: 550px !important;
        }

        @media screen and (min-width: 1200px) and (max-width: 1400px) {
            height: 470px !important;
        }

        @media screen and (min-width: 992px) and (max-width: 1200px) {
            height: 398px !important;
        }

        @media screen and (min-width: 768px) and (max-width: 992px) {
            height: 400px !important;
        }

        @media screen and (min-width: 576px) and (max-width: 768px) {
            height: 300px !important;
        }

        @media screen and (max-width: 576px) {
            width: calc(100vw - 30px);
            height: calc((100vw - 30px) * (9 / 16)) !important;
        }
    }

    .embed-cover-photo {
        width: 100%;
        height: auto;
        margin-top: 15px;
        object-fit: contain;
    }

    .blog-content-screen-container {
        .cover-container {
            padding: 0px !important;
            height: 300px;
            display: flex;

            .black-overlay {
                width: 100%;
                position: absolute;
                background: rgba(0, 0, 0, 0.65);
                height: 300px;
                z-index: 99;
            }

            img {
                width: 100%;
                object-position: center;
                height: 300px;
                object-fit: cover;
                align-items: center;
                z-index: 98;
            }

            h1.article-title {
                display: flex;
                text-align: center;
                font-family: inherit;
                font-size: 35px;
                font-weight: bold;
                justify-content: center;
                align-items: center;
                position: absolute;
                width: 100%;
                height: 300px;
                z-index: 100;
                color: white;
            }
        }

        .post-and-side-container-fluid {
            background-color: white;
            min-height: 100vh;
            width: 100vw;

            .post-and-side-container {
                height: 100%;
                padding: 0px !important;

                .post-and-side-row {
                    .post-container {
                        background-color: white;
                        padding: 45px 15px 30px 15px !important;

                        @media screen and (max-width: 576px) {
                            padding: 0px 15px 30px 15px !important;
                        }

                        @media screen and(max-width: 1200px) {
                            border-right-width: 0px;
                        }

                        .image-container {
                            width: 100%;
                            margin-top: 15px;
                            border: 3px solid black;

                            img {
                                width: 100%;
                                object-fit: cover;
                            }
                        }


                        .title-container {
                            margin-top: 40px;
                            letter-spacing: 0.3px;
                            font: 700 42px/55px Ubuntu;
                            font-weight: bolder;
                            margin-bottom: 10px;
                            letter-spacing: -2px;

                            @media screen and (max-width: 576px) {
                                font: 700 33px/42px Ubuntu;
                            }
                        }

                        .subtitle-container {
                            letter-spacing: -1.5px;
                            font: 500 28px/41px Ubuntu;
                            color: black;
                            margin-bottom: 15px;
                        }

                        .date-container {
                            font: 300 18px/22px Ubuntu;
                            font-weight: normal;
                            color: #171717;
                            letter-spacing: -1px;
                            margin-bottom: 15px;
                        }

                        article {
                            font: 400 21px/29px Ubuntu;
                            letter-spacing: 0px;
                            color: black;

                            strong {
                                font: 700 21px/29px Ubuntu;

                            }

                            a {
                                color: black;
                                font-weight: bolder;
                                text-decoration: underline;
                                cursor: pointer;
                            }

                            figure {
                                &:first-of-type {
                                    display: none;
                                }

                                img {
                                    width: 100%;
                                    object-fit: contain;
                                    display: block;
                                }
                            }

                            h1 {
                                font: 500 40px/45px Ubuntu;
                                color: black;

                                &:first-child {
                                    display: none;
                                }
                            }

                            h2 {
                                font: 500 35px/40px Ubuntu;
                                color: black;

                                &:first-of-type {
                                    display: none;
                                }
                            }

                            h3 {
                                font: 500 30px/35px Ubuntu;
                                color: black;
                                letter-spacing: -1.5px;

                                &:first-of-type {
                                    display: none;
                                }
                            }

                            p {
                                color: rgb(19, 19, 19);
                                font: 400 22px/41px Ubuntu;
                                letter-spacing: -0.5px;
                                width: 100%;
                                height: auto;

                                a {
                                    font: 400 22px/41px Ubuntu;
                                    color: black;
                                    font-weight: bold;
                                    letter-spacing: -1px;
                                }
                            }

                            ul {
                                margin-top: 40px;
                                margin-bottom: 40px;

                                li {
                                    font: 400 22px/41px Ubuntu !important;
                                    color: black;
                                    font-weight: bold;
                                    letter-spacing: -1px;

                                    a {
                                        font: 400 22px/41px Ubuntu !important;
                                        color: black;
                                        font-weight: bold;
                                        letter-spacing: -1px;
                                    }
                                }
                            }

                            figure {
                                font: 400 15px/41px Ubuntu !important;
                                color: gray;
                                font-weight: bold;

                                figcaption {
                                    text-align: center;
                                    width: '100%'
                                }
                            }

                            strong {
                                color: rgb(0, 0, 0);
                                font-weight: bold;
                            }

                            pre {
                                padding: 15px;
                                overflow: scroll;
                                background-color: black;
                                height: fit-content;
                                max-width: 100%;
                                color: white;
                                //-ms-overflow-style: none;
                                /* IE and Edge */
                                scrollbar-width: 1px;

                                /* Firefox */
                                &::-webkit-scrollbar {
                                    display: none;
                                    /* for Chrome, Safari, and Opera */
                                }
                            }

                            img {
                                width: 100%;
                                object-fit: contain;
                                display: block;
                            }

                            iframe {
                                width: calc(100%);
        height: 550px !important;
        margin-top: 15px;

        @media screen and (min-width: 1400px) {
            height: 550px !important;
        }

        @media screen and (min-width: 1200px) and (max-width: 1400px) {
            height: 470px !important;
        }

        @media screen and (min-width: 992px) and (max-width: 1200px) {
            height: 398px !important;
        }

        @media screen and (min-width: 768px) and (max-width: 992px) {
            height: 400px !important;
        }

        @media screen and (min-width: 576px) and (max-width: 768px) {
            height: 300px !important;
        }

        @media screen and (max-width: 576px) {
            width: calc(100vw - 30px);
            height: calc((100vw - 30px) * (9 / 16)) !important;
        }
                            }
                        }
                    }

                }
            }
        }
    }
}